import {__} from "@wordpress/i18n";
import { Component as ReactComponent } from "@wordpress/element";
import { TableCard } from '@woocommerce/components';

export class ShipmentsTable extends ReactComponent {
    
	constructor(props) {
		super(props);			
	}
		
	render() {				
		
		const shipments = this.props.shipments_by_status;
		const shipments_by_providers = this.props.shipments_by_providers;
		
		const tableData = {
			shipments: shipments.map(item =>
			[
				{display: item.shipment_status, value: item.shipment_status},
				{display: item.total, value: item.total},
				{display: item.percentage, value: item.percentage},                    
			]),
			shipments_by_providers: shipments_by_providers.map(item =>
			[
				{display: item.shipping_provider, value: item.shipping_provider},
				{display: item.total, value: item.total},
				{display: item.percentage, value: item.percentage},
				{display: item.average, value: item.average},	
			]),			
        };	
		
        return <div className="woocommerce-dashboard__columns">
					<TableCard
						title="Shipments by Status"
						headers={ [
							{ label: 'Tracking Status', key: 'shipment_status', isLeftAligned: true, required: true },
							{ label: 'Shipments', key: 'total', isLeftAligned: true },
							{ label: '% of total', key: 'percentage', isLeftAligned: true },
						] }
						rows={ tableData.shipments }
						rowsPerPage={ 100 }
						totalRows={ tableData.shipments.length }
					/>
					<TableCard
						title="Shipments by Provider"
						headers={ [
							{ label: 'Shipping Provider', key: 'shipping_provider', isLeftAligned: true, required: true },
							{ label: 'Shipments', key: 'shipments', isLeftAligned: true },
							{ label: '% of total', key: 'total', isLeftAligned: true },	
							{ label: 'Avg Length', key: 'average', isLeftAligned: true },								
						] }
						rows={ tableData.shipments_by_providers }
						rowsPerPage={ 100 }
						totalRows={ tableData.shipments_by_providers.length }
					/>	
			</div>					 		
    }
}