import {__} from "@wordpress/i18n";
import {Component as ReactComponent} from "@wordpress/element";
import { Chart } from '@woocommerce/components';
import { getChartTypeForQuery } from '@woocommerce/date';

export class ShipmentsChart extends ReactComponent {
    
	constructor(props) {
		super(props);			
	}
	
	render() {    
	
	const shipments_by_date = this.props.shipments_by_date;
	
    return <div class="ts_analytics_graph">
				<Chart data={ shipments_by_date } title="Shipments  by Date" layout="item-comparison" chartType={ getChartTypeForQuery( this.props.query ) } legendPosition="null" />				
			</div>
    }
}