// src/index.js
import {Component as ReactComponent, Fragment} from '@wordpress/element';
import {ChartPlaceholder, ReportFilters, SummaryList, SummaryListPlaceholder, SummaryNumber, TablePlaceholder} from '@woocommerce/components';
import { applyFilters } from '@wordpress/hooks';
import {__} from '@wordpress/i18n';
import {appendTimestamp, getCurrentDates, getDateParamsFromQuery, isoDateFormat} from '@woocommerce/date';
import apiFetch from '@wordpress/api-fetch';
import {default as Currency} from '@woocommerce/currency';
import {CURRENCY as storeCurrencySetting} from '@woocommerce/settings';
import {ShipmentsChart} from '../ShipmentsChart/ShipmentsChart';
import {ShipmentsTable} from '../ShipmentsTable/ShipmentsTable';

export class TSReport extends ReactComponent {
	
	constructor(props) {
		super(props);				
		
		this.handleDateChange = this.handleDateChange.bind(this);
		this.onFilterSelect = this.onFilterSelect.bind(this);
				
		const dateQuery = this.createDateQuery(this.props.query);
		const storeCurrency = new Currency(storeCurrencySetting);
		const queryParameters = this.getQueryParameters(dateQuery);
		
		this.state = {
			dateQuery: dateQuery,
			filterquery: '',
			currency: storeCurrency,			
			data: { loading: true }
		};
		
		this.fetchData(this.state.dateQuery,this.state.filterquery);			
	}	
	
	fetchData(dateQuery,filterQuery) {
		
		const endPoints = {
			'shipments_count': '/wc/v3/ts-analytics/shipments_count',
			'shipments_by_status': '/wc/v3/ts-analytics/shipments_by_status',
			'shipments_by_providers': '/wc/v3/ts-analytics/shipments_by_providers',
			'shipments_by_date': '/wc/v3/ts-analytics/shipments_by_date',			
			'get_shipments_providers': '/wc/v3/ts-analytics/get_shipments_providers',
		};	
		
		const queryParameters = this.getQueryParameters(dateQuery);
		
		const shipments_count_path = endPoints.shipments_count + queryParameters + filterQuery;
		const shipments_status_path = endPoints.shipments_by_status + queryParameters + filterQuery;
		const shipments_by_providers_path = endPoints.shipments_by_providers + queryParameters + filterQuery;
		const shipments_by_date_path = endPoints.shipments_by_date + queryParameters + filterQuery;
		const shipments_providers_path = endPoints.get_shipments_providers;
		
		Promise.all([
			apiFetch({path: shipments_count_path}),	
			apiFetch({path: shipments_status_path}),
			apiFetch({path: shipments_by_providers_path}),
			apiFetch({path: shipments_by_date_path}),
			apiFetch({path: shipments_providers_path}),			
		])
			.then(([shipments_count, shipments_by_status, shipments_by_providers, shipments_by_date, shipments_providers]) => {								
			
				const data = this.prepareData( shipments_count, shipments_by_status, shipments_by_providers, shipments_by_date, shipments_providers );
				
				this.setState({data: data})
			})
			.catch(err => console.log(err));
	}
	
	/**
     * Parses a query and returns an object representing a date query that is used to handle date range changes.
     */
	createDateQuery(query) {
		const {period, compare, before, after} = getDateParamsFromQuery(query);
		const {primary: primaryDate, secondary: secondaryDate} = getCurrentDates(query);
		return {period, compare, before, after, primaryDate, secondaryDate};
	}
	
	getQueryParameters(dateQuery) {
        const afterDate = encodeURIComponent(appendTimestamp(dateQuery.primaryDate.after, "start"));
        const beforeDate = encodeURIComponent(appendTimestamp(dateQuery.primaryDate.before, "end"));		
        return `?after=${afterDate}&before=${beforeDate}&interval=day&order=asc&per_page=100&_locale=user`;
    }
	
	prepareData(shipments_count, shipments_by_status,shipments_by_providers, shipments_by_date, shipments_providers) {
		
		let data;
		
		if ( shipments_count['total_count'] && shipments_count['active_count'] && shipments_count['delivered_count'] && shipments_count['avg_shipping_length'] ) {	
			data = {
				totals: {
					total_shipments: shipments_count['total_count'],
					active_shipments: shipments_count['active_count'],
					delivered_shipments: shipments_count['delivered_count'],
					avg_shipping_length: shipments_count['avg_shipping_length'],
				}
			};
			
		} else {
			data = {				
				totals: {
					total_shipments: 0,
					active_shipments: 0,
					delivered_shipments: 0,
					avg_shipping_length: 0,
				}
			};
			
		}
		
		if ( shipments_by_status.length > 0 ) {
			data.shipments_by_status = shipments_by_status;	
		} else {
			data.shipments_by_status = [];	
		}
		
		if ( shipments_by_providers.length > 0 ) {
			data.shipments_by_providers = shipments_by_providers;	
		} else {
			data.shipments_by_providers = [];	
		}
		
		if ( shipments_by_date.length > 0 ) {
			data.shipments_by_date = shipments_by_date;	
		} else {
			data.shipments_by_date = [];	
		}
		
		data.shipments_providers = shipments_providers;	
		data.loading = false;		
		return data;
	}
	
	handleDateChange(newQuery) {
		const newDateQuery = this.createDateQuery(newQuery);
		this.setState({dateQuery: newDateQuery});		
		this.fetchData(newDateQuery,this.state.filterquery);
	}
	
	onFilterSelect( filterdata ) {		
		
		if ( filterdata.shipment_status ) { 
			const filterquery = '&shipment_status='+filterdata.shipment_status;
			this.setState({filterquery: filterquery});
			this.fetchData( this.state.dateQuery , filterquery  );
		} else if ( filterdata.shipment_providers ) {
			const filterquery = '&shipment_provider='+filterdata.shipment_providers;
			this.setState({filterquery: filterquery});
			this.fetchData( this.state.dateQuery , filterquery  );
		} else {
			const filterquery = '';
			this.setState({filterquery: filterquery});
			this.fetchData( this.state.dateQuery , ''  );
		}			
	}
	
    render() {				
		
		const {data, currency, dateQuery} = this.state;	
		const isRequesting = false;
		
		const shipment_status_data = [
			{ label: 'All', value: 'all' },
			{ label: 'In Transit', value: 'in_transit' },
			{ label: 'Available For Pickup', value: 'available_for_pickup' },
			{ label: 'Out For Delivery', value: 'out_for_delivery' },
			{ label: 'Failed Attempt', value: 'failure' },
			{ label: 'On Hold', value: 'on_hold' },
			{ label: 'Exception', value: 'exception' },
			{ label: 'Return To Sender', value: 'return_to_sender' },
			{ label: 'Delivered', value: 'delivered' },			
		];
		
		const ORDERS_REPORT_FILTERS_FILTER = 'woocommerce_admin_ts_analytics_report_filters';		
		const filters = applyFilters( ORDERS_REPORT_FILTERS_FILTER, [
			{
				label: __( 'Shipment Status', 'woocommerce-admin' ),
				staticParams: [],
				param: 'shipment_status',
				showFilters: () => true,
				defaultValue: 'all',
				filters: shipment_status_data,
			},
			{
				label: __( 'Shipment Providers', 'woocommerce-admin' ),
				staticParams: [],
				param: 'shipment_providers',
				showFilters: () => true,
				defaultValue: 'all',
				filters: data.shipments_providers,
			},
		] );
		
		const reportFilters =
			<ReportFilters
				dateQuery={this.state.dateQuery}
				query={this.props.query}
				path={this.props.path}			
				currency={this.state.currency}
				onDateSelect={this.handleDateChange}
				isoDateFormat={isoDateFormat} 
				filters={ filters }	
				onFilterSelect={ this.onFilterSelect }				
			/>;
				
		if (this.state.data.loading) { 
			return <Fragment>
				<SummaryListPlaceholder numberOfItems={4}/>
			</Fragment>
		} else {
			return <Fragment>
				{reportFilters}
				<SummaryList>
					{() => [
						<SummaryNumber key='total_Shipments'
									value={data.totals.total_shipments}
									label={__('Total Shipments', 'trackship-for-woocommerce')}/>,
						<SummaryNumber key='active_Shipments'
									value={data.totals.active_shipments}
									label={__('Active Shipments', 'trackship-for-woocommerce')}/>,
						<SummaryNumber key='delivered_Shipments'
									value={data.totals.delivered_shipments}
									label={__('Delivered', 'trackship-for-woocommerce')}/>,
						<SummaryNumber key='avg_shipment_length'
									value={data.totals.avg_shipping_length}
									label={__('Avg Shipment Length', 'trackship-for-woocommerce')}/>
					]}
				</SummaryList>
				<ShipmentsChart chartData={0}
				dateRange={dateQuery.primaryDate.range}
				shipments_by_date ={data.shipments_by_date}
				currency={currency}
				query={this.props.query} />
				<ShipmentsTable
				shipments_by_status={data.shipments_by_status}
				shipments_by_providers ={data.shipments_by_providers}
				dateRange={dateQuery.primaryDate.range}
				currency={currency}/>
			</Fragment>
		}
	}
}