// Import SCSS entry file so that webpack picks up changes
import './index.scss';
import {addFilter} from '@wordpress/hooks';
import {__} from '@wordpress/i18n';
import {TSReport} from "./components/TSReport/TSReport";

addFilter('woocommerce_admin_reports_list', 'trackship-analytics', (reports) => {
    return [
        ...reports,
        {
            report: 'trackship-analytics',
            title: __('TrackShip Analytics', 'trackship-for-woocommerce'),
            component: TSReport
        },
    ];
});
